"use client";

import { useEffect } from "react";
import { supabaseBrowser } from "@/lib/supabase/client";
import { useRouter } from "next/navigation";

export default function AuthListener() {
  const router = useRouter();

  useEffect(() => {
    const { data: listener } = supabaseBrowser.auth.onAuthStateChange(async (event, session) => {
      if (event === "SIGNED_IN") {
        const token = session?.access_token;
        if (token) {
          document.cookie = `sb-access-token=${token}; path=/; max-age=3600;`;
        }

        const user = session?.user;
        if (!user) return;

        const { data: profile } = await supabaseBrowser.from("profiles").select("*").eq("id", user.id).maybeSingle();
        if (!profile) {
          router.push("/auth/complete-profile");
        } else {
          router.push("/dashboard");
        }
      }

      if (event === "SIGNED_OUT") {
        document.cookie = "sb-access-token=; path=/; expires=Thu, 01 Jan 1970 00:00:00 GMT";
        router.push("/auth");
      }
    });

    return () => {
      listener.subscription.unsubscribe();
    };
  }, [router]);

  return null;
}
